/* eslint-disable no-undef */
import React, { useState } from "react"
import { Section, Heading, Layout } from "components"
import { Formik } from "formik"
import { Form } from "antd"
import TextInput from "components/forms/TextInput"
import { SubmitButton } from "formik-antd"
// import { contentfulManagement } from "App"
import { Contentful } from "helpers/constants"
import CKEditor from "@ckeditor/ckeditor5-react"
import ClassicEditor from "@ckeditor/ckeditor5-build-classic"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { fetchArticleCategories, fetchArticles } from "store/actions/articleActions"
import SelectInput from "components/forms/SelectInput"
import { createClient } from "contentful-management"

const contentfulManagement = createClient({
	accessToken: process.env.REACT_APP_MANAGEMENT_ACCESS_TOKEN
})

export default function Reads() {
	const dispatch = useDispatch()
	const [richText, setRichText] = useState("")
	const categories = useSelector(({ article }) => article.articleCategories)
	const articles = useSelector(({ article }) => article.articles)

	const handleSubmitArticle = (values, { setSubmitting }) => {
		const selectedCategory = categories.find((item) => item.value === values.category) || {}
		contentfulManagement
			.getSpace(Contentful.SPACE_ID)
			.then(({ createEntry }) => {
				createEntry("article", {
					fields: {
						title: { "en-US": values.title },
						content: { "en-US": values.content },
						// category: { "en-US": selectedCategory },
						tags: { "en-US": ["sempardak", "javascript"] },
						dateCreated: { "en-US": Date.now() }
					}
				})
			})
			.then((entry) => console.log({ entry }))
			.catch((err) => console.error({ err }))
			.finally(() => setSubmitting(false))
	}

	useEffect(() => {
		dispatch(fetchArticleCategories())
		dispatch(fetchArticles())
	}, [dispatch])

	console.log({ categories, articles })

	return (
		<Layout>
			<Section centered maxWidth="1200px">
				<Heading content="Cudy Reads" />

				<Formik initialValues={{ title: "" }} onSubmit={handleSubmitArticle}>
					{({ handleSubmit }) => (
						<Form layout="vertical" onSubmit={handleSubmit}>
							<TextInput name="title" placeholder="The title..." label="Title" />
							<SelectInput name="category" placeholder="Choose category..." options={categories} label="Category" />
							<TextInput textarea name="content" placeholder="The content..." label="Content" />
							{/* <CKEditor
								editor={ClassicEditor}
								onChange={(e, editor) => {
									const data = editor.getData()
									setRichText(data)
								}}
							/> */}
							<SubmitButton type="primary">Add article</SubmitButton>
						</Form>
					)}
				</Formik>
			</Section>
		</Layout>
	)
}
