import { gql } from "apollo-boost"

export const ALL = gql`
	query GET_ALL($role: String!, $status: String) {
		getBookings(role: $role, status: $status) {
			pvid
			startTime
			endTime
			location
			status
			notes
			classUrl
			student {
				pvid
				userPvid
				email
				firstName
				lastName
			}
			tutor {
				pvid
				userPvid
				email
				firstName
				lastName
			}
			subjectInfo {
				pvid
				subject
				level
			}
		}
	}
`

