export const ONE = gql`
	query ONE($id: Int!) {
		getBooking(id: $id) {
			pvid
			startTime
			endTime
			location
			status
			notes
			student {
				pvid
				userPvid
				email
				firstName
				lastName
				country
				phone
				address
			}
			tutor {
				pvid
				userPvid
				email
				firstName
				lastName
				country
				phone
				address
			}
		}
	}
`

export const ADD = gql`
	mutation ADD_BOOKING(
		$tutorPvid: String!
		$subjectPvid: String!
		$level: String!
		$startTime: String!
		$endTime: String!
		$location: String!
		$status: String!
		$notes: String
		$price: Float!
		$duration: Float!
	) {
		addBooking(
			tutorPvid: $tutorPvid
			subjectPvid: $subjectPvid
			level: $level
			startTime: $startTime
			endTime: $endTime
			location: $location
			status: $status
			notes: $notes
			price: $price
			duration: $duration
		) {
			pvid
			startTime
			endTime
			location
			status
			notes
			price
			duration
			student {
				pvid
				email
				userPvid
				firstName
				lastName
				country
				phone
				address
			}
			tutor {
				pvid
				email
				userPvid
				firstName
				lastName
				country
				phone
				address
			}
		}
	}
`

export const UPDATE = gql`
	mutation UPDATE(
		$id: Int!
		$subjectPvid: String
		$startTime: String
		$endTime: String
		$location: String
		$status: String
		$notes: String
	) {
		updateBooking(
			id: $id
			subjectPvid: $subjectPvid
			startTime: $startTime
			endTime: $endTime
			location: $location
			status: $status
			notes: $notes
		) {
			pvid
			price
		}
	}
`
