import React, { useEffect } from "react"
import { Row, Col } from "antd"
import { Link, withRouter } from "react-router-dom"
import { Section, Heading, Button, Card, Alert, GifPlayer } from "components"
import { Trans } from "@lingui/macro"
import { country } from "App"

function TestimonialSuccess({ location, history }) {
	const state = location.state || {}

	useEffect(() => {
		if (!state.success) history.push("/profile/add_testimonial")
	}, [history, location, state.success])

	return (
		<Section centered textAlign="center" style={{ marginTop: "4em" }}>
			<Row type="flex" justify="center" align="middle">
				<Col lg={12} xs={24}>
					<Card title="" autoHeight>
						<Section textAlign="center" centered>
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								content={<Trans>Thank you! :)</Trans>}
								subheader={
									<Trans>
										Your testimonial has been submitted. And you are also successfully registered. See the email we've
										just sent you to get started.
									</Trans>
								}
								marginBottom="3em"
							/>
							<Alert message={<Trans>Don't forget to check your spam/junk folder too :)</Trans>} type="info" showIcon />
						</Section>
						<Section textAlign="center" centered paddingHorizontal="normal">
							<Link to={`/${country}`}>
								<Button type="ghost" icon="home">
									&nbsp;<Trans>Back to Home</Trans>
								</Button>
							</Link>
						</Section>
					</Card>
				</Col>
			</Row>
		</Section>
	)
}

export default withRouter(TestimonialSuccess)
