import React, { useState } from "react"
import axios from "axios"
import { Typography, Row, Col } from "antd"
import { useQuery, useMutation } from "@apollo/react-hooks"
import { Elements } from "@stripe/react-stripe-js"
import { loadStripe } from "@stripe/stripe-js"
import { useStripe } from "@stripe/react-stripe-js"
import { Trans } from "@lingui/macro"
import moment from "moment"
import { Button, Section, Loading } from "components"
import { formatCurrency, newClient } from "helpers"
import { GET_BALANCE_INFO, mutateUpdateProfile } from "queries/profile"
import { UPDATE } from "queries/booking"

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PUBLISH_KEY)

const ProcessPayment = ({ country, currency, bookingData, handleClose }) => {
	const stripe = useStripe()
	const amount = bookingData.amount
	const { tutor, student, subjectInfo, ...rest } = bookingData
	const stripeFee = amount * 0.029
	const midtransFee = 4000
	const apaylaterFee = 0

	const handlePayment = (name, fee = 0) => {
		const paymentName =
			name === "stripe"
				? `stripe-${country}`
				: name === "midtrans"
				? `midtrans-${country}`
				: name === "apaylater"
				? `apaylater-${country}`
				: ""
		const data = {
			paymentName,
			applicationName: "cudy-i3",
			tutorData: { ...tutor, pvid: tutor.userPvid },
			studentData: { ...student, pvid: student.userPvid },
			productData: {
				...rest,
				product_name: `Payment booking for tutor name ${tutor.firstName}`,
				product_description: `Payment booking for tutor ${tutor.firstName} with subject ${subjectInfo.subject} - ${
					subjectInfo.level
				} start at ${moment(bookingData.startTime).toDate()}`,
				product_start_date: moment(bookingData.startTime).toDate(),
				product_end_date: moment(bookingData.endTime).toDate(),
				product_location: bookingData.location,
				country,
				amount,
				fee,
				total: amount + fee
			}
		}

		axios
			.post(`/payments/init`, data)
			.then(({ data }) => {
				if (name === "stripe") {
					stripe.redirectToCheckout({ sessionId: data.data.result.id })
					handleClose()
				} else if (name === "midtrans" || name === "apaylater") {
					window.open(data.data.result.link, "_top")
				}
			})
			.catch((error) => console.log(error))
	}

	return (
		<Row gutter={24}>
			<Col lg={24} style={{ marginBottom: "1.2em" }}>
				<Row>
					<Col lg={16}>
						<Typography.Paragraph style={{ marginBottom: "4px" }}>
							<Trans>Amount </Trans>
							{` ${currency} ${formatCurrency(amount)}`}
						</Typography.Paragraph>
						<Typography.Paragraph style={{ marginBottom: "4px" }}>
							<Trans>Transaction fee 2.9% </Trans>
							{` ${currency} ${formatCurrency(stripeFee)}`}
						</Typography.Paragraph>
						<Typography.Paragraph style={{ marginBottom: "4px" }}>{`Total ${currency} ${formatCurrency(
							amount + stripeFee
						)}`}</Typography.Paragraph>
					</Col>
					<Col lg={8}>
						<Button type="primary" icon="credit-card" onClick={() => handlePayment("stripe", stripeFee)}>
							&nbsp; <Trans>Credit Card</Trans>
						</Button>
					</Col>
				</Row>
			</Col>
			{country === "id" && (
				<Col lg={24} style={{ marginBottom: "1.2em" }}>
					<Row>
						<Col lg={16}>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>
								<Trans>Amount </Trans>
								{` ${currency} ${formatCurrency(amount)}`}
							</Typography.Paragraph>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>
								<Trans>Transaction fee</Trans>
								{` ${currency} ${formatCurrency(midtransFee)}`}
							</Typography.Paragraph>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>{`Total ${currency} ${formatCurrency(
								amount + midtransFee
							)}`}</Typography.Paragraph>
						</Col>
						<Col lg={8}>
							<Button type="primary" icon="bank" onClick={() => handlePayment("midtrans", midtransFee)}>
								&nbsp; <Trans>Bank Transfer</Trans>
							</Button>
						</Col>
					</Row>
				</Col>
			)}
			{country === "sg" && amount >= 30 && (
				<Col lg={24} style={{ marginBottom: "1.2em" }}>
					<Row>
						<Col lg={16}>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>
								<Trans>Amount </Trans>
								{` ${currency} ${formatCurrency(amount)}`}
							</Typography.Paragraph>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>
								<Trans>Transaction fee </Trans>
								{` ${currency} ${formatCurrency(apaylaterFee)}`}
							</Typography.Paragraph>
							<Typography.Paragraph style={{ marginBottom: "4px" }}>{`Total ${currency} ${formatCurrency(
								amount + apaylaterFee
							)}`}</Typography.Paragraph>
						</Col>
						<Col lg={8}>
							<Button type="primary" icon="bank" onClick={() => handlePayment("apaylater", apaylaterFee)}>
								&nbsp; <Trans>APayLater</Trans>
							</Button>
						</Col>
					</Row>
				</Col>
			)}
		</Row>
	)
}

const Payments = ({ bookingData, ...props }) => {
	axios.defaults.baseURL = process.env.REACT_APP_PAYMENT_URL
	axios.defaults.headers.common["api-key"] = process.env.REACT_APP_PAYMENT_API_KEY

	const local_country = localStorage.getItem("country") || "sg"
	const country = bookingData.student.country
	const currency = country === "sg" ? "SGD" : country === "id" ? "IDR" : ""
	const [has_paid, set_has_paid] = useState(false)
	const [loading_update, set_loading_update] = useState(false)

	const [balanceInfo, setBalanceInfo] = useState({})

	const { loading } = useQuery(GET_BALANCE_INFO, {
		fetchPolicy: "network-only",
		onCompleted: async ({ getProfile }) => {
			if (getProfile) {
				console.log("getProfile", getProfile.balance)
				if (getProfile.hasBalance) {
					if (getProfile.balance > bookingData.price) {
						set_loading_update(true)
						try {
							await updateProfile({ variables: { balance: getProfile.balance - bookingData.price } })
							await updateBooking({ variables: { id: Number(bookingData.pvid), status: "paid" } })
						} catch (error) {
							console.log(error)
						}
						set_loading_update(false)
						set_has_paid(true)
					} else {
						bookingData.amount = Number(bookingData.price)
					}
				} else {
					bookingData.amount = Number(bookingData.price) * 8
				}
				setBalanceInfo(getProfile)
			}
		},
		onError(error) {
			console.log("GET_BALANCE_INFO", error)
		}
	})

	const [updateBooking] = useMutation(UPDATE, {
		client: newClient("booking"),
		onError: (error) => {
			console.log("updateBooking", error)
		}
	})

	const [updateProfile] = useMutation(mutateUpdateProfile, {
		client: newClient("account"),
		onError(error) {
			console.log("updateProfile", error)
		}
	})

	if (loading || loading_update) {
		return (
			<Section centered>
				<Loading />
			</Section>
		)
	}

	if (has_paid) {
		return (
			<Section centered>
				<Typography.Title className="mb2em">
					<Trans>Booking Success</Trans>
				</Typography.Title>
			</Section>
		)
	}

	if (local_country !== country) {
		return (
			<Section centered>
				<Typography.Paragraph className="mb2em">
					<Trans>Cannot process to payment with different country</Trans>
				</Typography.Paragraph>
			</Section>
		)
	}

	return (
		<Section centered>
			<Typography.Paragraph className="mb2em">
				{balanceInfo.hasBalance ? (
					<>
						<Trans>Your balance is </Trans>
						{` ${currency} ${formatCurrency(balanceInfo.balance)}`}
					</>
				) : (
					<Typography.Paragraph>
						<Typography.Paragraph>
							<Trans>This is your first time for making a payment for tutoring. You will be charged </Trans>
							{` ${currency} ${formatCurrency(bookingData.amount)} `}
							<Trans>for 8 lessons in advance, which will be stored as balance in your account</Trans>
						</Typography.Paragraph>
						<Typography.Paragraph>
							<Trans>
								If you only wish to have less than 8 lessons by this tutor, you may use your stored balance to make bookings
								with other tutors or services on the platform
							</Trans>
						</Typography.Paragraph>
					</Typography.Paragraph>
				)}
			</Typography.Paragraph>
			<Typography.Paragraph className="mb2em">
				<Trans>Please click button below to proces the payments</Trans>
			</Typography.Paragraph>
			{/* <Modal title={<Trans>Select payment method</Trans>} visible={showModal} onCancel={() => setShowModal(false)} footer={false}>
				<Elements stripe={stripePromise}>
					<ProcessPayment country={country} currency={currency} bookingData={bookingData} {...props} />
				</Elements>
			</Modal>
			<Button type="primary" icon="gold" onClick={() => setShowModal(!showModal)}>
				&nbsp; <Trans>Process to payment</Trans>
			</Button> */}
			<Elements stripe={stripePromise}>
				<ProcessPayment country={country} currency={currency} bookingData={bookingData} {...props} />
			</Elements>
		</Section>
	)
}

export default Payments
