import * as types from "../constants"
import { contentfulClient } from "App"

export const loadingArticle = () => ({ type: types.LOADING_ARTICLE })

export const fetchArticles = () => (dispatch) => {
	dispatch(loadingArticle())
	return contentfulClient
		.getEntries({ content_type: "article" })
		.then(({ items }) => {
			dispatch({ type: types.FETCH_ARTICLES, payload: items.map((item) => item.fields) })
		})
		.catch((err) => {
			dispatch({ type: types.FETCH_ARTICLES_ERROR })
			console.error(err)
		})
}

export const fetchArticleCategories = () => (dispatch) => {
	dispatch(loadingArticle())
	return contentfulClient
		.getEntries({ content_type: "articleCategory" })
		.then(({ items }) => {
			dispatch({ type: types.FETCH_ARTICLE_CATEGORIES, payload: items.map((item) => item.fields) })
		})
		.catch((err) => {
			dispatch({ type: types.FETCH_ARTICLE_CATEGORIES_ERROR })
			console.error(err)
		})
}
