import { gql } from "apollo-boost"

export const mutateBasicProfile = gql`
	mutation mutateBasicProfile($desc: String!, $address: String!) {
		updateBasicProfile(desc: $desc, address: $address)
	}
`

export const mutateUpdateProfile = gql`
	mutation mutateUpdateProfile(
		$firstName: String
		$lastName: String
		$dob: Date
		$nric: String
		$phone: String
		$gender: String
		$race: String
		$address: String
		$description: String
		$eventsFromGoogle: String
		$balance: Float
		$hasBalance: Boolean
	) {
		updateProfile(
			firstName: $firstName
			lastName: $lastName
			dob: $dob
			nric: $nric
			phone: $phone
			gender: $gender
			race: $race
			address: $address
			description: $description
			eventsFromGoogle: $eventsFromGoogle
			balance: $balance
			hasBalance: $hasBalance
		) {
			pvid
			firstName
			lastName
			dateModified
			dob
			nric
			phone
			gender
			race
			description
			address
		}
	}
`

