import React, { useState, useEffect, useCallback, useContext } from "react"
import styled from "styled-components"
import Headroom from "react-headroom"
import { Row, Col, Dropdown, Avatar, Icon, Menu, Badge, Select, Typography } from "antd"

import Button from "./Button"
import Search from "./forms/Search"
import Heading from "./Heading"
import { Link, withRouter, useHistory, useLocation } from "react-router-dom"
import NotificationPanel from "./common/NotificationPanel"
import { reviews } from "../dummy"
import { useLazyQuery, useMutation, useQuery } from "@apollo/react-hooks"
import { queryLogout } from "queries/auth"
import { mutateUserProfile } from "queries/profile"
import { unauthUser } from "store/actions/authActions"
import { fetchUserOptions } from "store/actions/userActions"
import { mobile, media, formatCurrency } from "helpers"
import Percentage from "./Percentage"
import ProfileCompletion from "./common/ProfileCompletion"
import MenuDrawer from "./common/MenuDrawer"
import { queryNotificationList } from "queries/user"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import DynamicIcon from "./common/DynamicIcon"
import { countries, countryMap } from "helpers/countryMapping"
import useAvatar from "helpers/hooks/useAvatar"
import { baseStyles } from "styles/base"
import Logo from "./Logo"
import { notifyMe, notifyBooking } from "helpers/notification"
import { handleChangeLanguage, country, SocketContext } from "App"
import { useBottomBarLinks } from "helpers/hooks/data"
import { SERVER_BASE_URL } from "helpers/constants"

/*global Beamer*/

function Navbar({ i18n }) {
	const history = useHistory()
	const location = useLocation()
	const { bottomBarLinks, isPageLifelong } = useBottomBarLinks()

	const { socket } = useContext(SocketContext)
	let notifList = []
	const [showDrawer, setDrawer] = useState(false)
	const [menuDrawer, setMenuDrawer] = useState(false)
	const [profileCompletionDrawer, setProfileCompletionDrawer] = useState(false)

	const detailsFromStorage = JSON.parse(localStorage.getItem("userDetails")) || {}
	const token = localStorage.getItem("token")
	const currency = detailsFromStorage.country === "sg" ? "SGD" : detailsFromStorage.country === "id" ? "IDR" : ""

	const [setSignout] = useLazyQuery(queryLogout, unauthUser)
	const [fetchUser, { data, loading: loadingUser }] = useMutation(mutateUserProfile, fetchUserOptions(history))

	const { data: notif, refetch, loading: loadingNotif } = useQuery(queryNotificationList, {
		fetchPolicy: "network-only"
	})

	if (notif && notif.listNotification) {
		notifList = notif.listNotification
	}

	const userData = (data || {}).getProfile || {}
	const avatar = (userData.avatar || [])[0] || {}
	const actualUploadedAva = userData && userData.avatar && (userData.avatar[userData.avatar.length - 1] || {}).path

	const userAvatar = useAvatar(avatar.path)
	const ava = (userData.avatar || []).length > 1 ? SERVER_BASE_URL + actualUploadedAva : userAvatar

	const handleSendKeywords = (value) => history.push(`/${country}/browse/tutors?keywords=${value}`)
	const handleOpenCompletionDrawer = () => {
		fetchUser()
		setProfileCompletionDrawer(true)
	}

	const { completionUser = {}, completionDetail = [] } = userData
	const percent = completionUser.completionPercent

	const pathIncludesCountryCode = Object.keys(countryMap).findIndex((item) => location.pathname.includes("/" + item))
	const previousRoute = pathIncludesCountryCode > -1 ? location.pathname.slice(3) : location.pathname

	const menu = (
		<Menu style={{ padding: "1.4em 1.2em", width: mobile ? "100%" : 280 }}>
			<Menu.Item key="hi" disabled>
				<ProfileHeading content={<Trans>Hi, {userData.firstName || userData.roleName}</Trans>} />
			</Menu.Item>
			<Menu.Item key="balance">
				{/* <ProfileHeading content={`${currency} ${formatCurrency(userData.balance)}`} /> */}
				<Button type="ghost" block>
					<DynamicIcon type="icon-dollar" /> {`${currency} ${formatCurrency(userData.balance)}`}
				</Button>
			</Menu.Item>
			{(detailsFromStorage.role || {}).code === "TTR0" && (
				<Menu.Item key="completion" onClick={handleOpenCompletionDrawer}>
					<ProfileComplete gutter={32} type="flex" align="middle">
						<Col lg={6}>
							<Percentage type="circle" percent={percent} width={50} />
						</Col>{" "}
						<Col lg={18}>
							<Heading
								content={percent === 100 ? <Trans>You've made it!</Trans> : <Trans>You're almost there</Trans>}
								subheader={percent === 100 ? <Trans>Your profile is complete</Trans> : <Trans>Complete your profile</Trans>}
								level={4}
								marginBottom="0"
							/>
						</Col>
					</ProfileComplete>
				</Menu.Item>
			)}
			{(detailsFromStorage.role || {}).code === "TTR0" && (
				<Menu.Item key="profile">
					<Link to={`/${country}/profile/me`}>
						<Trans>
							<Icon type="user" /> &nbsp; Your profile
						</Trans>
					</Link>
				</Menu.Item>
			)}
			<Menu.Item key="chats">
				<Link to={`/${country}/chats`}>
					<Trans>
						<Icon type="message" /> &nbsp; Chats
					</Trans>
				</Link>
			</Menu.Item>
			{(detailsFromStorage.role || {}).code === "STDN" && (
				<Menu.Item key="assignments">
					<Link to={`/${country}/assignments`}>
						<Trans>
							<Icon type="project" /> &nbsp; Your assignments
						</Trans>
					</Link>
				</Menu.Item>
			)}
			<Menu.Item key="settings">
				<Link to={`/${country}/profile/settings`}>
					<Trans>
						<Icon type="setting" /> &nbsp; Settings
					</Trans>
				</Link>
			</Menu.Item>
			<Menu.Item key="updates" id="show-updates" onClick={() => Beamer.show()}>
				<Trans>
					<Icon type="notification" />
					Cudy updates
				</Trans>
			</Menu.Item>
			<Menu.Item key="booking-list">
				<Link to={`/${country}/booking-list`}>
					<Trans>
						<Icon type="message" /> &nbsp; Booking List
					</Trans>
				</Link>
			</Menu.Item>
			<Menu.Item key="transaction-list">
				<Link to={`/${country}/transaction-list`}>
					<Trans>
						<Icon type="message" /> &nbsp; Transaction List
					</Trans>
				</Link>
			</Menu.Item>
			<Menu.Item key="logout" onClick={() => setSignout()}>
				<Trans>Logout</Trans>
			</Menu.Item>
		</Menu>
	)

	const handleFetchUser = useCallback(() => {
		if (token) fetchUser()
	}, [fetchUser, token])

	useEffect(() => {
		handleFetchUser()
	}, [handleFetchUser])

	useEffect(() => {
		socket.on("notification", (item) => {
			refetch()
			if (item.code === "BOOKING") {
				notifyBooking(item.reviewerName, item.remarks)
			} else {
				notifyMe(item.reviewerName, item.remarks)
			}
		})
	}, [refetch, socket])

	return (
		<StyledHeadRoom disableInlineStyles>
			<ProfileCompletion
				data={{ completionUser, completionDetail }}
				loading={loadingUser}
				placement="right"
				visible={profileCompletionDrawer}
				onClose={() => setProfileCompletionDrawer(false)}
			/>

			<MenuDrawer
				loading={loadingUser}
				data={{ userData, menuDrawer, reviews, notifList }}
				handlers={{
					showDrawer,
					setDrawer,
					setMenuDrawer,
					setSignout,
					fetchUser,
					handleSendKeywords,
					handleChangeLanguage
				}}
			/>

			<Header>
				<Row type="flex" justify="space-between">
					<Col lg={14}>
						<Row>
							<Col lg={6} xs={12}>
								<div className="main-logo">
									<StyledLogo onClick={() => history.push(`/${country}`)} width={mobile ? "60" : "100"} />
								</div>
							</Col>
							{!mobile && (
								<Col lg={18} xs={12}>
									<div className="search">
										<Search
											size="large"
											onSearch={handleSendKeywords}
											placeholder={`${i18n._(t`Search for`)} ${isPageLifelong ? "coaches" : "tutors"}...`}
										/>{" "}
										&nbsp;&nbsp;
										<Link to={`/${country}/${isPageLifelong ? `browse/tutors` : `lifelong/browse`}`}>
											<Button type="primary" size="default">
												<DynamicIcon type={`icon-${isPageLifelong ? "book" : "guitar"}`} />
												&nbsp; <Trans>Go to Cudy</Trans> {isPageLifelong ? " Academics" : " Lifelong"}
											</Button>
										</Link>
									</div>
								</Col>
							)}
						</Row>
					</Col>
					<Col lg={10} style={{ textAlign: !mobile && "right" }}>
						<Row type="flex" justify="end">
							<Col lg={12}>
								{!mobile && (
									<Select
										name="language"
										onChange={(value) => handleChangeLanguage(value)}
										style={{ width: 160, marginRight: "2em" }}
										value={localStorage.getItem("country") || "sg"}
									>
										{countries.map((item) => (
											<Select.Option key={item.key} value={item.key}>
												<DynamicIcon type={item.icon} size={16} />
												&nbsp; &nbsp; {item.name}
											</Select.Option>
										))}
									</Select>
								)}
							</Col>
							<Col lg={token ? 6 : 12} className="ta-right">
								{/* If it's logged in */}
								{token ? (
									mobile ? (
										<Button
											shape="circle"
											icon="more"
											size="default"
											type="primary"
											onClick={() => setMenuDrawer(!menuDrawer)}
										/>
									) : (
										<LoggedinItems>
											<Badge count={notifList.length || 0}>
												<Button
													onClick={() => setDrawer(!showDrawer)}
													shape="circle"
													icon="bell"
													size="default"
													type="dashed"
												/>
											</Badge>
											<NotificationPanel
												visible={showDrawer}
												onClose={() => setDrawer(false)}
												data={reviews}
												notifList={notifList}
												loading={loadingNotif}
											/>
											<Dropdown
												overlay={menu}
												placement="bottomRight"
												// onVisibleChange={(visible) => visible && fetchUser()}
											>
												<a href="#">
													<Avatar size="large" src={ava} /> <Icon type="down" />
												</a>
											</Dropdown>
										</LoggedinItems>
									)
								) : // If it's not logged in
								mobile ? (
									<>
										<Button type="ghost" size="default" onClick={() => history.push(`/${country}/login`)}>
											<Trans>Login</Trans>
										</Button>{" "}
										&nbsp;{" "}
										<Button
											shape="circle"
											icon="more"
											size="default"
											type="primary"
											onClick={() => setMenuDrawer(!menuDrawer)}
										/>
									</>
								) : (
									<UnloggedinItems>
										<Link to={{ pathname: `/${country}/login`, state: { previousRoute } }}>
											<Button type="ghost" size="default">
												<Trans>Login</Trans>
											</Button>
										</Link>
										&nbsp; &nbsp;
										<Link to={`/${country}/register`}>
											<Button type="primary" size="default">
												<Trans>Sign up</Trans>
											</Button>
										</Link>
									</UnloggedinItems>
								)}
							</Col>
						</Row>
					</Col>
				</Row>
			</Header>

			<BottomBar type="flex" justify={mobile ? "space-between" : "center"} gutter={16}>
				{bottomBarLinks.map((item) => (
					<Col xs={7} lg={2} key={item.key}>
						<Link to={{ pathname: "/" + country + item.pathname }}>
							<Typography className="typo ta-center">
								<Typography.Paragraph>
									<DynamicIcon type={item.icon} size={mobile ? 34 : 24} />
								</Typography.Paragraph>
								<Typography.Text type="secondary" className="fs-09em">
									{item.label}
								</Typography.Text>
							</Typography>
						</Link>
					</Col>
				))}
			</BottomBar>
		</StyledHeadRoom>
	)
}

export default withI18n()(Navbar)

//         
//       
//                
//                        
//             
//             

const Header = styled.header`
	background-color: #fff;
	#google_translate_element {
		margin-right: 2em;
		display: inline-block;
		+ * {
			display: inline-block;
		}
	}
	.main-logo {
		height: auto;
		img {
			cursor: pointer;
		}
	}
`

const BottomBar = styled(Row)`
	position: relative;
	padding: 1em 1.5em;
	line-height: 40px;
	background-color: ${baseStyles.lightGrey.two};
	box-shadow: ${baseStyles.boxShadow.hover};
	.ant-col {
		line-height: 1.1;
		.typo {
			transition: all 0.2s ease;
			> .ant-typography {
				margin-bottom: 0.5em;
			}
			&:hover {
				transform: translateY(-2px);
			}
		}
	}

	&& {
		${media.mobile`
			padding: 1em 1.5em;
			flex-wrap: nowrap;
			overflow-x: scroll;
			width: auto;
			-webkit-overflow-scrolling: touch;
			&::-webkit-scrollbar {
				display: none;
			}
		`}
	}
`

const StyledHeadRoom = styled(Headroom)`
	height: 0;
	.headroom {
		top: 0;
		left: 0;
		right: 0;
		z-index: 11;
		> header {
			padding: 0 5em;
		}
	}
	.headroom--unfixed {
		position: relative;
		transform: translateY(0);
	}
	.headroom--scrolled {
		transition: transform 0.3s ease;
	}
	.headroom--unpinned {
		position: fixed;
		transform: translateY(-100%);
	}
	.headroom--pinned {
		position: fixed;
		transform: translateY(0%);
		> header {
			background-color: #fff;
		}
	}

	${media.mobile`
		.headroom {
			> header {
				padding: 0 2em;
			}
		}
	`}
`

const StyledLogo = styled(Logo)`
	margin-right: 5px;
	cursor: pointer;
`

const LoggedinItems = styled.div`
	display: flex;
	justify-content: flex-end;
	align-items: center;
	min-width: 120px;
	.ant-badge {
		margin-right: 2em;
		button {
			border-color: #d9d9d9;
			+ sup {
				top: 5px;
			}
		}
	}

	${media.mobile`
        display: block;
        .ant-menu {
            border-right: none;
            margin-bottom: 2em;
        }
        .ant-badge {
            margin-right: 0;
            display: block;
        }
    `}
`

const UnloggedinItems = styled.div`
	/* display: inline-block; */
	text-align: right;

	${media.mobile`
        > * {
            display: block;
        }
    `}
`

const ProfileHeading = styled(Heading).attrs({
	level: 4,
	marginBottom: "0"
})`
	h4.ant-typography {
		font-size: 1.2em;
	}
`

const ProfileComplete = styled(Row)`
	.ant-typography {
		h4 {
			font-size: 1em;
		}
	}
`
