import React from "react"
import { FormItem as Item, Input, InputNumber } from "formik-antd"
import { Input as InputAntd, InputNumber as InputAntdNumber, Form } from "antd"
import styled from "styled-components"
import { Field } from "formik"

const FormItem = styled(Item)`
	&& {
		margin-bottom: ${({ mb }) => mb || "1em"};
		.ant-input-number,
		input {
			width: 100%;
		}
		.ant-input-prefix {
			color: rgba(0, 0, 0, 0.25);
		}
		.ant-form-item-label {
			label {
				font-size: ${({ reverse }) => reverse && "11px"};
				color: ${({ reverse }) => reverse && "#bbb"};
				text-transform: ${({ reverse }) => reverse && "uppercase"};
				line-height: ${({ reverse }) => reverse && "1.4"};
				font-weight: ${({ reverse }) => reverse && "600"};
			}
		}
		.help-text {
			font-size: 12px;
			color: #999;
		}
	}
`

const StyledTextarea = styled(Input.TextArea)`
	&& {
		border: ${({ transparent }) => transparent && "none"};
		padding-left: ${({ transparent }) => transparent && 0};
		resize: none;
		&:focus {
			border: ${({ transparent }) => transparent && "none"};
			box-shadow: ${({ transparent }) => transparent && "none"};
		}
	}
`

export const SpecialComponent = ({ field, helpText, ...props }) => {
	field.value = props.value

	return (
		<Form.Item
			{...field}
			{...props}
			value={props.value}
			validateStatus={props.error && "error"}
			help={props.error && props.error}
			label={props.label}
		>
			<InputAntd {...field} {...props} value={props.value} onChange={props.onChange} id={props.id} onPressEnter={() => null} />
			{helpText && <span className="help-text grey">{helpText}</span>}
		</Form.Item>
	)
}

function TextInput({ helpText, independent, special, name, label, ...props }) {
	let Inputee = independent ? InputAntd : Input
	let InputeeNumber = independent ? InputAntdNumber : InputNumber

	if (special) return <Field {...props} helpText={helpText} id={props.id} name={name} component={SpecialComponent} />

	if (independent) {
		if (props.number)
			return (
				<Form.Item name={name} label={label}>
					<InputAntdNumber {...props} name={name} style={{ width: "100%" }} />
				</Form.Item>
			)

		if (props.textarea)
			return (
				<Form.Item name={name} label={label}>
					<InputAntd.TextArea {...props} name={name} style={{ width: "100%" }} />
				</Form.Item>
			)

		return (
			<Form.Item
				help={props.error}
				validateStatus={props.error && "error"}
				name={name}
				label={label}
				onPressEnter={props.onPressEnter}
			>
				<InputAntd {...props} name={name} style={{ width: "100%" }} />
			</Form.Item>
		)
	}

	return (
		<FormItem
			name={name}
			style={props.style}
			reverse={props.reverse}
			extra={props.extra}
			label={label}
			mb={props.mb}
			className={props.className}
		>
			{props.textarea ? (
				<>
					<StyledTextarea {...props} name={name} />
					{helpText && <span className="help-text">{helpText}</span>}
				</>
			) : props.password ? (
				<>
					<Inputee.Password {...props} name={name} />
					{helpText && <span className="help-text">{helpText}</span>}
				</>
			) : props.number ? (
				<>
					<InputeeNumber {...props} name={name} style={{ width: "100%" }} />
					{helpText && <span className="help-text">{helpText}</span>}
				</>
			) : (
				<>
					<Inputee {...props} name={name} />
					{helpText && <span className="help-text">{helpText}</span>}
				</>
			)}
		</FormItem>
	)
}

export default TextInput
