import React from "react"
import { Icon } from "antd"

const scriptUrl = "//at.alicdn.com/t/font_1290431_43y2ppn0un6.js"

export default function DynamicIcon({ size, color, ...props }) {
	const TheIcon = Icon.createFromIconfontCN({ scriptUrl, extraCommonProps: { style: { fontSize: size || 20, color } } })

	return <TheIcon {...props} size={size} />
}
