/* eslint-disable no-undef */
import React, { useState, Suspense } from "react"
import { Section, Calendar, Button } from "components"
import { Badge, Typography, message } from "antd"
import moment from "moment"
import styled from "styled-components"
import { baseStyles } from "styles/base"
import { mutateUpdateProfile } from "queries/profile"
import { newClient, mobile, renderError } from "helpers"
import { useMutation } from "react-apollo"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import ApiCalendar from "react-google-calendar-api"
import { gapi } from "gapi-script"

const DateModal = React.lazy(() => import("./DateModal"))

const StyledBadge = styled(Badge)`
	.ant-badge-status-text {
		font-size: 12px;
		margin-left: 3px;
		> p {
			p {
				font-size: 0.9em;
				color: ${baseStyles.greyColor};
				line-height: 1;
			}
		}
		> .ant-typography {
			width: 60px;
		}
	}
`

function TutorCalendar({ teachingDate = [], unauthorizedUser, onEditMode, i18n, userData, refetchData }) {
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const [selectedData, setSelectedData] = useState("")
	const [isSignedToGoogle, setIsSignedToGoogle] = useState(ApiCalendar.sign)
	const [showDetail, setShowDetail] = useState(false)

	const [updateProfile] = useMutation(mutateUpdateProfile, {
		client: newClient("account"),
		onCompleted: () => {
			refetchData && refetchData()
			message.success(i18n._(t`Event google imported`))
		},
		onError: (err) => renderError(err),
	})

	const handleSelectDate = (date) => {
		const dateString = date.startOf("day").toISOString()
		const selectedData = teachingDate.filter(
			(t) =>
				dateString >= moment(t.startTime).startOf("day").toISOString() &&
				dateString <= moment(t.endTime).startOf("day").toISOString()
		)
		setShowDetail(true)
		setSelectedData({ date, schedule: selectedData })
	}

	const normalizeEvent = (items = []) => {
		return JSON.stringify(
			items.map((r) => ({
				id: r.id,
				title: r.summary,
				startTime: r.start.dateTime,
				endTime: r.end.dateTime,
				notes: r.description,
				location: r.location,
				status: r.status,
			}))
		)
	}

	const handleLogout = () => {
		gapi.auth2
			.getAuthInstance()
			.signOut()
			.then(() => window.location.reload(true))
	}

	const handleFetchScheduleList = async () => {
		if (!isSignedToGoogle) {
			await gapi.auth2.getAuthInstance().signIn()
			const { result } = await ApiCalendar.listUpcomingEvents(10)
			// setScheduleFromGoogle(result.items)
			updateProfile({ variables: { eventsFromGoogle: normalizeEvent(result.items) } })
			setIsSignedToGoogle(true)
		} else {
			const { result } = await ApiCalendar.listUpcomingEvents(10)
			// setScheduleFromGoogle(result.items)
			updateProfile({ variables: { eventsFromGoogle: normalizeEvent(result.items) } })
		}
	}

	const renderDateCell = (value) => {
		const valString = value.startOf("day").toISOString()
		return teachingDate.map((item) => {
			if (
				valString >= moment(item.startTime).startOf("day").toISOString() &&
				valString <= moment(item.endTime).startOf("day").toISOString()
			) {
				return <StyledBadge status="success" text={<Typography.Text ellipsis>{item.title}</Typography.Text>} />
			}
			return null
		})
	}

	const renderEvents = () => {
		return (
			<>
				{!unauthorizedUser &&
					(isSignedToGoogle ? (
						<Section style={{ marginTop: "1rem" }}>
							<Button style={{ marginRight: "1rem" }} social="google" icon="google" onClick={handleFetchScheduleList}>
								&nbsp; <Trans>Sync events from GCal</Trans>
							</Button>
							<Button type="primary" onClick={handleLogout}>
								<Trans>Logout from Google</Trans>
							</Button>
						</Section>
					) : (
						<Button style={{ marginTop: "1rem" }} social="google" icon="google" onClick={handleFetchScheduleList}>
							&nbsp; <Trans>Import events from GCal</Trans>
						</Button>
					))}
				<Calendar dateCellRender={renderDateCell} onSelect={handleSelectDate} />
			</>
		)
	}

	return (
		<Section paddingHorizontal={0} noPadding={mobile} /* style={{ pointerEvents: "none" }} */>
			{/* <ResultSection
				type="comingsoon"
				title={<Trans>We've been working hard for this schedule feature. Please stay tuned.</Trans>}
			/> */}

			<Suspense fallback="Loading...">
				<DateModal
					visible={showDetail}
					unauthorizedUser={unauthorizedUser}
					userDetails={userDetails}
					data={selectedData}
					userData={userData}
					onCancel={() => setShowDetail(false)}
					onAdded={refetchData}
				/>
			</Suspense>

			{renderEvents()}
		</Section>
	)
}

export default withI18n()(TutorCalendar)
