/* eslint-disable no-undef */
import React, { useEffect, useState, createContext } from "react"
import { BrowserRouter } from "react-router-dom"
import { ApolloProvider } from "@apollo/react-hooks"
import { Provider } from "react-redux"
import useSocket from "use-socket.io-client"
import { BackTop, ConfigProvider } from "antd"
import { I18nProvider } from "@lingui/react"
import { setupI18n } from "@lingui/core"
import moment from "moment"
import { Trans } from "@lingui/macro"
import { ApolloClient } from "apollo-client"
import { InMemoryCache } from "apollo-cache-inmemory"
import { createUploadLink } from "apollo-upload-client"
import { createClient } from "contentful-management"
import "moment/locale/id"

import { createAppStore } from "./store"
import ScrollToTop from "components/common/ScrollToTop"
import useIdle from "helpers/hooks/useIdle"
import { Modal, Heading, Button } from "components"
import catalogID from "locales/id/messages.js"
import catalogEN from "locales/en/messages.js"
import Routes from "routes"
import idID from "antd/es/locale/id_ID"
import enUS from "antd/es/locale/en_US"
import { countryMap } from "helpers/countryMapping"
import { BASE_URL } from "helpers/constants"

const contentful = require("contentful")

const store = createAppStore()
export const AppContext = createContext({})
export const ContentfulContext = createContext(null)

const catalogMap = { id: catalogID, en: catalogEN }
export let handleChangeLanguage = null
const i18n = localStorage.getItem("i18n")
export const country = localStorage.getItem("country") || "sg"
export const outsideI18n = setupI18n({ catalogs: catalogMap, language: i18n || "en" })

moment.locale(i18n || "en")

export const SocketContext = React.createContext(null)

const client = new ApolloClient({
	cache: new InMemoryCache(),
	link: createUploadLink({
		uri: `${BASE_URL}/profile`,
		headers: { "auth-token": localStorage.getItem("token") }
	})
})

export const contentfulClient = contentful.createClient({
	space: process.env.REACT_APP_CONTENTFUL_SPACE_ID,
	accessToken: process.env.REACT_APP_CONTENTFUL_ACCESS_TOKEN
})

export const contentfulManagement = createClient({
	accessToken: process.env.REACT_APP_MANAGEMENT_ACCESS_TOKEN
})

function App() {
	const [catalogs, setCatalogs] = useState(catalogMap)
	const [language, setLanguage] = useState(i18n)
	const [valueFromMarketplace, setValueFromMarketplace] = useState({})
	const isIdle = useIdle({ timeToIdle: 60 * 1000 * 10, inactivityEvents: [] })

	const [socket] = useSocket(BASE_URL)

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	let pathArr = window.location.pathname.split("/")

	handleChangeLanguage = (language, cb) => {
		const selectedLang = language === "id" ? "id" : "en"
		return import(`@lingui/loader!./locales/${selectedLang}/messages.json`)
			.then((catalog) => {
				localStorage.setItem("i18n", selectedLang)
				localStorage.setItem("country", language)
				setCatalogs({ ...catalogs, [selectedLang]: catalog })
				setLanguage(selectedLang)
			})
			.then(() => {
				let pathArr = window.location.pathname.split("/")
				if (cb && cb !== null) cb()
				else window.location.replace("/" + countryMap[language].key + "/" + pathArr.slice(2).join("/"))
			})
	}

	const handleRefresh = () => window.location.reload(true)

	document.addEventListener("copy", (event) => {
		const pagelink = `-- Read more at: ${document.location.href}`
		event.clipboardData.setData("text", document.getSelection() + pagelink)
		event.preventDefault()
	})

	const handleNewMessage = (cb) => {
		socket.on("new_message", (message) => {
			cb(message)
		})
	}

	useEffect(() => {
		// socket.on("new_message", message => {
		// 	console.log({ message })
		// })
		if (localStorage.getItem("token")) {
			socket.emit("join_channel", { channel: userDetails.token })
		}
	}, [language, pathArr, socket, userDetails.token])

	return (
		<div className="main-wrapper">
			<I18nProvider language={language} catalogs={catalogs}>
				<ContentfulContext.Provider value={contentfulClient}>
					<AppContext.Provider value={{ onMarketplace: { valueFromMarketplace, setValueFromMarketplace } }}>
						<ConfigProvider hi locale={country === "id" ? idID : enUS}>
							<SocketContext.Provider value={{ socket, onNewMessage: handleNewMessage }}>
								<Provider store={store}>
									<ApolloProvider client={client}>
										<BrowserRouter>
											<ScrollToTop />
											<BackTop />
											<Modal visible={isIdle} footer={false} closable={false}>
												<Heading
													level={4}
													content={<Trans>Your session is not active</Trans>}
													subheader={
														<Trans>
															You haven't done anything for a while. Please refresh the page to restore your
															session.
														</Trans>
													}
													marginBottom="3em"
												/>
												<Trans>
													<Button type="primary" icon="reload" onClick={handleRefresh}>
														Refresh page
													</Button>
												</Trans>
											</Modal>
											<Routes language={language} country={country} /> {/* This is the routes */}
										</BrowserRouter>
									</ApolloProvider>
								</Provider>
							</SocketContext.Provider>
						</ConfigProvider>
					</AppContext.Provider>
				</ContentfulContext.Provider>
			</I18nProvider>
		</div>
	)
}

export default App
