import React, { useState } from "react"
import { Section, Heading, ButtonLink, Logo, Modal } from "components"
import { Form, Row, Col, message, Checkbox } from "antd"
import TextInput from "components/forms/TextInput"
import SelectInput from "components/forms/SelectInput"
import { useQuery, useMutation } from "@apollo/react-hooks"
import { queryFetchSubjects } from "queries/data"
import { newClient, client } from "helpers"
import { Formik } from "formik"
import { Rate, SubmitButton, FormItem, FormikDebug } from "formik-antd"
import { withRouter } from "react-router"
import CheckboxInput from "components/forms/CheckboxInput"
import { MUTATE_ADD_TESTIMONIAL } from "queries/profile"
import useError from "helpers/hooks/useError"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import Alert from "components/Alert"

const token = localStorage.getItem("token")

const AddTestimonials = React.memo(({ location, history, i18n }) => {
	const [registerModal, setRegisterModal] = useState(false)
	const [tempInfo, setTempInfo] = useState({})
	const [agreed, setAgreed] = useState(false)

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const country = userDetails.country || "sg"

	const { data: dataSubjects = {} } = useQuery(queryFetchSubjects, {
		client: client.programmeClient,
		variables: { country },
	})

	const subjectOptions = (dataSubjects.getProgrammeSubjectList || []).map((item) => ({
		value: Number(item.pvid),
		label: item.name,
	}))

	const [addTestimonial] = useMutation(MUTATE_ADD_TESTIMONIAL, { client: newClient() })

	const params = new URLSearchParams(location.search)
	// const tutorCode = params.get("tutor_code") || ""
	const referralCode = params.get("referral_code") || ""

	const handleAddTestimonial = (values, { setSubmitting }) => {
		setSubmitting(true)
		const { tc, ...newUserValues } = values
		values = {
			...newUserValues,
			referralCode,
		}
		if (!token) {
			setRegisterModal(true)
			setTempInfo(values)
		} else {
			addTestimonial({
				variables: { ...values, email: userDetails.email, reviewerPvid: Number(userDetails.pvid) },
			})
				.then(() => {
					message.loading(i18n._(t`Please wait...`)).then(() => message.success(i18n._(t`Your testimonial has been sent`)))
				})
				.catch((err) => useError(err))
		}
		setSubmitting(false)
	}

	const handleSubmitTestimonial = (values, { setSubmitting }) => {
		setSubmitting(true)
		const { tc, ...newUserValues } = values
		values = {
			...tempInfo,
			reviewerPvid: Number(userDetails.pvid) || 0,
			newUser: newUserValues,
		}
		addTestimonial({ variables: values })
			.then(() => {
				message.loading(<Trans>Please wait...</Trans>).then(() => {
					history.push({
						pathname: `/${country}/profile/add_testimonial/success`,
						state: { success: true },
					})
				})
			})
			.catch((err) => useError(err))
			.finally(() => setSubmitting(false))
	}

	return (
		<Section paddingHorizontal={0}>
			<Modal centered visible={registerModal} onCancel={() => setRegisterModal(false)} footer={null}>
				<Section paddingHorizontal={0}>
					<Heading
						content={<Trans>One more step...</Trans>}
						subheader={<Trans>We need a few details about yourself</Trans>}
						level={4}
						marginBottom="3em"
					/>
					<Formik
						onSubmit={handleSubmitTestimonial}
						initialValues={{ firstName: "", lastName: "", tc: false }}
						validate={validateRegister(i18n)}
					>
						{({ handleSubmit, isValid }) => (
							<Form layout="vertical" onSubmit={handleSubmit} className="mb2em">
								<Row gutter={16}>
									<Col lg={12}>
										<TextInput
											name="firstName"
											placeholder={i18n._(t`Your first name`)}
											label={<Trans>Your first name</Trans>}
										/>
									</Col>
									<Col lg={12}>
										<TextInput
											name="lastName"
											placeholder={i18n._(t`Your last name`)}
											label={<Trans>Your last name</Trans>}
										/>
									</Col>
								</Row>
								<Row style={{ marginBottom: "3em" }}>
									<Col lg={24}>
										<TextInput
											name="email"
											type="email"
											placeholder={i18n._(t`E.g. jennysalim@example.com`)}
											label={<Trans>Your email</Trans>}
										/>
										<Checkbox checked={agreed} onChange={(e) => setAgreed(e.target.checked)}>
											<Trans>
												I have read and agreed to the{" "}
												<a href="https://www.cudy.co/tnc" target="_blank" rel="noreferrer noopener">
													terms and conditions
												</a>
												, as well as the{" "}
												<a href="https://www.cudy.co/privacy" target="_blank" rel="noreferrer noopener">
													privacy policy
												</a>{" "}
												of the site.
											</Trans>
										</Checkbox>
									</Col>
								</Row>
								<SubmitButton size="large" disabled={!isValid || !agreed}>
									<Trans>Register and add testimonial</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<ButtonLink size="large" onClick={() => setRegisterModal(false)}>
									<Trans>Cancel</Trans>
								</ButtonLink>
							</Form>
						)}
					</Formik>
					<Alert
						type="info"
						showIcon
						message={<Trans>Please check your email inbox to activate the testimonial once submitted</Trans>}
					/>
				</Section>
			</Modal>

			<Row type="flex" justify="center" style={{ marginTop: "3em" }}>
				<Col lg={10}>
					<Logo onClick={() => history.push(`/${country}`)} mb="3em" />
					<Heading
						content={<Trans>Add testimonials</Trans>}
						subheader={<Trans>Say what you'd like to say about the tutor</Trans>}
						marginBottom="3em"
					/>
					<Formik
						onSubmit={handleAddTestimonial}
						validate={validate(i18n)}
						initialValues={{ programmeSubjectPvid: "", comment: "" }}
					>
						{({ handleSubmit }) => (
							<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "3em" }}>
								<SelectInput
									name="programmeSubjectPvid"
									label={<Trans>Subject they teached</Trans>}
									placeholder={i18n._(t`Choose subject they teached`)}
									options={[{ value: "", label: <Trans>Select one option...</Trans> }, ...subjectOptions]}
								/>
								<FormItem name="rating" label={<Trans>Rate your experience with the tutor</Trans>}>
									<Rate name="rating" allowHalf={true} allowClear={true} />
								</FormItem>
								<TextInput
									textarea
									name="comment"
									label={<Trans>Your testimonials</Trans>}
									placeholder={i18n._(t`Please be good, and give them a good comment/review`)}
									rows={5}
								/>
								<SubmitButton type="primary" size="large">
									<Trans>Add your testimonial</Trans>
								</SubmitButton>
								&nbsp;{" "}
							</Form>
						)}
					</Formik>
					<Section textAlign="center" centered>
						<ButtonLink size="large" onClick={() => history.push(`/${country}`)}>
							<Trans>Cancel and go to Home</Trans>
						</ButtonLink>
					</Section>
				</Col>
			</Row>
		</Section>
	)
})

const validate = (i18n) => (values) => {
	let errors = {}

	if (!values.programmeSubjectPvid) errors.programmeSubjectPvid = i18n._(t`You have to choose the subject`)
	if (!values.rating) errors.rating = i18n._(t`Don't forget the rating`)
	if (!values.comment) errors.comment = i18n._(t`Don't forget the review`)

	return errors
}

const validateRegister = (i18n) => (values) => {
	let errors = {}

	if (!values.firstName) errors.firstName = i18n._(t`Don't forget your first name`)
	if (!values.lastName) errors.lastName = i18n._(t`Don't forget your last name`)
	if (!values.email) errors.email = i18n._(t`Don't forget the email`)

	return errors
}

export default withI18n()(withRouter(AddTestimonials))
