import React from "react"
import { Checkbox, FormItem } from "formik-antd"

export default function CheckboxInput({ group = false, name, label, children, ...props }) {
	if (group)
		return (
			<FormItem name={name} label={label}>
				<Checkbox.Group {...props} name={name} />
			</FormItem>
		)

	return (
		<FormItem name={name} label={label}>
			<Checkbox {...props} name={name}>
				{children}
			</Checkbox>
		</FormItem>
	)
}
